declare var google;

export class Task {
  constructor(
    public id: number,
    public position: object,
    public time: number,
    public done: boolean
  ) { }
}

export const getRandomPositionInMoscow = () => {
  // Garden ring area
  const x1 = 55.769646564537965;
  const x4 = 55.736511311407995;
  const y1 = 37.59708126362102;
  const y4 = 37.645146449167896;

  const n1 = Math.random() * (x4 - x1) + x1;
  const n2 = Math.random() * (y4 - y1) + y1;

  return new google.maps.LatLng(n1, n2);
}

export const tasks: Task[] = [
  { id: 1, position: getRandomPositionInMoscow(), time: 1200, done: false },
  { id: 2, position: getRandomPositionInMoscow(), time: 1232, done: false },
  { id: 3, position: getRandomPositionInMoscow(), time: 2300, done: false },
];



