import { Component } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';
import { tasks } from '../../app/tasks';
import { Inspector, inspectors, getRandomPositionInMoscow } from '../../app/inspectors';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  tasks = tasks;
  inspectors = inspectors;
  private readonly timeToCut = 600;

  constructor(private toastCtrl: ToastController, public navCtrl: NavController) {

  }

  addInspector() {
    const id = this.inspectors.length + 1;
    inspectors.sort((a,b) => b.tasks.length - a.tasks.length);
    let rem = [];

    if (inspectors[0].tasks.length > 1) {
      rem = inspectors[0].tasks.splice(-1,1);
    }

    this.inspectors.push(new Inspector(id, `Controller #${id}`, getRandomPositionInMoscow(), rem, {}));
  }

  removeInspector() {
    if (this.inspectors.length === 1) {
      this.toastCtrl.create({
        message: 'You can\'t remove all controllers',
        duration: 3000,
        position: 'top'
      }).present();
      return false;
    }

    const removed = this.inspectors.splice(-1,1)[0];

    if (!removed.tasks.length) {
      return false;
    }

    inspectors.sort((a,b) => a.tasks.length - b.tasks.length);

    inspectors[0].tasks = inspectors[0].tasks.concat(removed.tasks);
  }

  remove10Minuts() {
    let time = this.timeToCut;
    let tasksDone = [];
    for (const task of tasks) {
      time = time - task.time;

      if (time <= 0) {
        task.time = Math.abs(time);
        break;
      } else {
        task.done = true;
        tasksDone.push(task.id);
      }
    }

    if (!tasksDone.length) {
      return false;
    }

    for (const inspector of inspectors) {
      for (let i = 0; i < inspector.tasks.length; i++) {
        for (const taskDone of tasksDone) {
          if (taskDone === inspector.tasks[i].id) {
            inspector.tasks.splice(i, 1);
          }
        }
      }
    }
  }

  getTaskById(id) {
    return tasks.find(task => task.id === id);
  }

}



