import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { tasks } from '../../app/tasks';
import { inspectors } from '../../app/inspectors';

declare var google;

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  @ViewChild('map') mapElement: ElementRef;
  map: any;
  currentPosition: Object;
  tasks = tasks;
  inspectorPoints: any[] = [];
  inspectorsWays: any[] = [];
  private directionsService = new google.maps.DirectionsService();
  // private const directionsDisplay: Object = new google.maps.DirectionsRenderer();

  constructor(private toastCtrl: ToastController, public navCtrl: NavController, public geolocation: Geolocation) {

  }

  ionViewDidLoad() {
    this.loadMap();
  }

  ionViewWillEnter() {
    if (!!this.inspectorPoints.length) {
      this.deleteInspectors();
      this.showInspectors();
    }
  }

  loadMap() {
    // if (!this.geolocation) {
    //   return false;
    // }

    // this.geolocation.getCurrentPosition().then((position) => {
      let latLng = new google.maps.LatLng(55.75550537217034, 37.62074902110544);
      // let latLng = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);

      let mapOptions = {
        center: latLng,
        zoom: 13,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        streetViewControl: false,
        fullscreenControl: false,
        mapTypeControl: false
      }

      this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);

      let marker = new google.maps.Marker({
        map: this.map,
        animation: google.maps.Animation.DROP
      });

      this.map.addListener('click', (e) => {
        this.currentPosition = e.latLng;
        marker.setPosition(e.latLng);
      });

      // this.directionsDisplay.setMap(this.map);

      this.showInspectors();

    // }, (err) => {
    //   console.log(err);
    // });
  }

  newTask() {
    if (!this.currentPosition) {
      return false;
    }

    if (this.currentPosition === tasks[tasks.length - 1].position) {
      this.toastCtrl.create({
        message: 'This task was added already',
        duration: 3000,
        position: 'top'
      }).present();
      return false;
    }

    this.inspectorsWays = [];

    for (const inspector of inspectors) {
      let lastPosition = inspector.position;

      if (inspector.tasks.length > 0) {
        lastPosition = inspector.tasks[inspector.tasks.length - 1].position;
      }

      const request = {
        origin: lastPosition,
        destination: this.currentPosition,
        provideRouteAlternatives: false,
        travelMode: 'WALKING',
        drivingOptions: {
          departureTime: new Date()
        },
        unitSystem: google.maps.UnitSystem.IMPERIAL
      };

      this.directionsService.route(request, (result, status) => {
        result.inspectorId = inspector.id;
        this.inspectorsWays.push(result);

        inspector.data.possibleWay = result;
        inspector.data.possibleTime = inspector.tasks.reduce((p,c) => {
          return p + c.time || 0;
        }, inspector.data.possibleWay.routes[0].legs[0].duration.value);

        if (this.inspectorsWays.length === inspectors.length) {
          this.findBestWay();
        }
      });
    }
  }

  findBestWay() {
    this.inspectorsWays.sort((a,b) => a.routes[0].legs[0].duration.value - b.routes[0].legs[0].duration.value);
    inspectors.sort((a,b) => a.data.possibleTime - b.data.possibleTime);

    const newTaskData = {
      id: tasks.length + 1,
      position: this.currentPosition,
      time: inspectors[0].data.possibleWay.routes[0].legs[0].duration.value + 300, // 5 min = 300s
      done: false
    };

    tasks.push(newTaskData);

    inspectors[0].tasks.push(newTaskData);

    this.toastCtrl.create({
      message: 'Task was added successfully',
      duration: 3000,
      position: 'top'
    }).present();

    // this.directionsDisplay.setDirections(inspectors[0].possibleWay);
  }

  showInspectors() {
    for (const inspector of inspectors) {
      const position = inspector.position;
      inspector.position = position;

      let point = new google.maps.Marker({
          map: this.map,
          position: position,
          icon: {
              path: google.maps.SymbolPath.CIRCLE,
              fillColor: '#00F',
              fillOpacity: 0.6,
              strokeColor: '#00A',
              strokeOpacity: 0.9,
              strokeWeight: 1,
              scale: 7
          }
      });

      this.inspectorPoints.push(point);
    }
  }

  deleteInspectors() {
    for (const point of this.inspectorPoints) {
      point.setMap(null);
    }
    this.inspectorPoints = [];
  }
}



