declare var google;

export class Inspector {
  constructor(
    public id: number,
    public name: string,
    public position: object,
    public tasks: any[],
    public data: any
  ) { }
}

export const getRandomPositionInMoscow = () => {
  // Garden ring area
  const x1 = 55.769646564537965;
  const x4 = 55.736511311407995;
  const y1 = 37.59708126362102;
  const y4 = 37.645146449167896;

  const n1 = Math.random() * (x4 - x1) + x1;
  const n2 = Math.random() * (y4 - y1) + y1;

  return new google.maps.LatLng(n1, n2);
}

export const inspectors: Inspector[] = [
  {
    id: 1,
    name: 'Controller #1',
    position: getRandomPositionInMoscow(),
    tasks: [
      {
        id:1,
        time:1000,
        position: getRandomPositionInMoscow()
      }
    ],
    data: {},
  },
  {
    id: 2,
    name: 'Controller #2',
    position: getRandomPositionInMoscow(),
    tasks: [
      {
        id:2,
        time:1000,
        position: getRandomPositionInMoscow()
      },
      {
        id:3,
        time:1000,
        position: getRandomPositionInMoscow()
      }
    ],
    data: {},
  },
];



